body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0 !important;
  padding: 0 !important;
  font-family: "Roboto Mono";
}

.mainDiv{

  width: 100%;
  height: 94vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: white;
}

.centerDiv{
  width: 25%;
  height: 80vh;
  background-color: white;
  overflow: auto;
  
}


.todo-heading{

  font-family: "Roboto Mono";
  padding: 5px 0px;
  color: black;
  font-weight: bold;
}

input[type="text"]{

  border: none;
  background-color: transparent;
  border-bottom: 2px solid black;
}
input:focus, select:focus{
  outline: none;
}
.inputAndBtnStyle{
  
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 50px 0px !important;
}

ol li{

  padding-left: 0px;
  text-align: left;
  font-size: 20px;
  font-weight: 500;
  display: flex;
  align-items: center;
  color: black;
  text-transform: capitalize;
}

.todostyle{
  /* border: 1px solid red; */
  margin-top: 20px;
  margin-right: 27px;
  display: flex;
  justify-content: left;
  flex-direction: row;
  align-items: center;
  margin-bottom: 10px !important;
  
}

.max-width{
  padding: 0px 235px !important;
}

.footerStyle{
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: space-around;
  height: 6vh;
  background-color: black;
}

.footerStyle p{
  color: white;
  font-size: 11px;
}